var MonteCarloAgent, P1, P2, Tree, argmax, choose, ref;

ref = require('./game'), P1 = ref.P1, P2 = ref.P2;

choose = function(a) {
  return a[Math.floor(Math.random() * a.length)];
};

argmax = function(xs, testF) {
  var curr, i, len, max, val, x;
  max = Number.MIN_SAFE_INTEGER;
  for (i = 0, len = xs.length; i < len; i++) {
    x = xs[i];
    curr = testF(x);
    if (curr > max) {
      max = curr;
      val = x;
    }
  }
  return val;
};

Tree = (function() {
  function Tree(data) {
    this.data = data != null ? data : {};
  }

  Tree.prototype.observe = function(node) {
    var base;
    return (base = this.data)[node] != null ? base[node] : base[node] = [0, 0];
  };

  Tree.prototype.observed = function(node) {
    return this.data[node] != null;
  };

  Tree.prototype.addResult = function(node, result) {
    var stats;
    stats = this.data[node];
    stats[0] += 1;
    return stats[1] += result;
  };

  Tree.prototype.value = function(node) {
    var stats;
    stats = this.data[node];
    return stats[1] / stats[0];
  };

  Tree.prototype.visitCount = function(node) {
    return this.data[node][0];
  };

  return Tree;

})();

MonteCarloAgent = (function() {
  function MonteCarloAgent(options) {
    var ref1, ref2;
    if (options == null) {
      options = {};
    }
    this.timeFrameMs = (ref1 = options.timeFrameMs) != null ? ref1 : 5000;
    this.tree = (ref2 = options.tree) != null ? ref2 : new Tree;
  }

  MonteCarloAgent.prototype.nextAction = function(state) {
    var _, action, begin, bestAction, game, i, lastNode, len, mul, node, ref1, result, successors;
    this.tree.observe(state);
    begin = Date.now();
    while (Date.now() - begin < this.timeFrameMs) {
      game = [];
      node = state;
      while ((node != null) && this.tree.observed(node)) {
        game.push(lastNode = node);
        node = this.select(node);
      }
      node = lastNode;
      if (!node.isTerminal()) {
        node = this.expand(node, game);
      }
      result = this.simulate(node);
      for (i = 0, len = game.length; i < len; i++) {
        node = game[i];
        this.tree.addResult(node, result);
      }
    }
    mul = state.nextPlayer === P1 ? +1 : -1;
    successors = (function() {
      var j, len1, ref1, results;
      ref1 = state.possibleActions();
      results = [];
      for (j = 0, len1 = ref1.length; j < len1; j++) {
        action = ref1[j];
        results.push([action, state.play(action)]);
      }
      return results;
    })();
    ref1 = argmax(successors, (function(_this) {
      return function(arg) {
        var action, nextState;
        action = arg[0], nextState = arg[1];
        if (_this.tree.observed(nextState)) {
          return mul * _this.tree.value(nextState);
        } else {
          return Number.MIN_SAFE_INTEGER;
        }
      };
    })(this)), bestAction = ref1[0], _ = ref1[1];
    return bestAction;
  };

  MonteCarloAgent.prototype.select = function(node) {
    var T, nodeVisits;
    if (node.isTerminal()) {
      return null;
    }
    T = 30;
    nodeVisits = this.tree.visitCount(node);
    if (nodeVisits >= T) {
      return this.uct(node, nodeVisits);
    } else {
      return this.play(node);
    }
  };

  MonteCarloAgent.prototype.uct = function(node, nodeVisits) {
    var C, action, nodeLog, successors;
    C = 0.7;
    successors = (function() {
      var i, len, ref1, results;
      ref1 = node.possibleActions();
      results = [];
      for (i = 0, len = ref1.length; i < len; i++) {
        action = ref1[i];
        results.push(node.play(action));
      }
      return results;
    })();
    nodeLog = Math.log(nodeVisits);
    return argmax(successors, (function(_this) {
      return function(succNode) {
        _this.tree.observe(succNode);
        return _this.tree.value(succNode) + C * Math.sqrt(nodeLog / (_this.tree.visitCount(succNode) || 1e-6));
      };
    })(this));
  };

  MonteCarloAgent.prototype.expand = function(node, game) {
    var action, actions, i, len, successor;
    actions = node.possibleActions();
    for (i = 0, len = actions.length; i < len; i++) {
      action = actions[i];
      successor = node.play(action);
      if (!this.tree.observed(successor)) {
        this.tree.observe(successor);
        game.push(successor);
        return successor;
      }
    }
    return this.play(node);
  };

  MonteCarloAgent.prototype.simulate = function(node) {
    var winner;
    while (!node.isTerminal()) {
      node = this.play(node);
    }
    winner = node.winner();
    switch (winner) {
      case P1:
        return +1;
      case P2:
        return -1;
      default:
        return 0;
    }
  };

  MonteCarloAgent.prototype.play = function(node) {
    return node.play(choose(node.possibleActions()));
  };

  return MonteCarloAgent;

})();

module.exports = {
  P1: P1,
  P2: P2,
  MonteCarloAgent: MonteCarloAgent,
  Tree: Tree
};
