var Limit, MAX, MIN, MinimaxAgent, P1, P2, ref, ref1;

ref = require('./game'), P1 = ref.P1, P2 = ref.P2;

ref1 = [P1, P2], MAX = ref1[0], MIN = ref1[1];

Limit = Number.MAX_SAFE_INTEGER;

MinimaxAgent = (function() {
  function MinimaxAgent(depth) {
    this.depth = depth != null ? depth : Limit;
    this.rootAgent = MAX;
  }

  MinimaxAgent.prototype.nextAction = function(state) {
    var _, bestAction, ref2;
    this.rootAgent = state.nextAgent();
    ref2 = this.minimax(state), _ = ref2[0], bestAction = ref2[1];
    return bestAction;
  };

  MinimaxAgent.prototype.minimax = function(state, α, β, ply) {
    var isTerminal;
    if (α == null) {
      α = -Limit;
    }
    if (β == null) {
      β = +Limit;
    }
    if (ply == null) {
      ply = 0;
    }
    isTerminal = state.isTerminal();
    if (!isTerminal && state.nextAgent() === this.rootAgent) {
      ++ply;
    }
    if (ply > this.depth || isTerminal) {
      return [state.utility(), null];
    } else if (state.nextAgent() === MAX) {
      return this.maxi(state, α, β, ply);
    } else {
      return this.mini(state, α, β, ply);
    }
  };

  MinimaxAgent.prototype.maxi = function(state, α, β, ply) {
    var _, a, action, i, len, nextValue, ref2, ref3, ref4, ref5, successor, v;
    ref2 = [-Limit, null], v = ref2[0], a = ref2[1];
    ref3 = state.possibleActions();
    for (i = 0, len = ref3.length; i < len; i++) {
      action = ref3[i];
      successor = state.play(action);
      ref4 = this.minimax(successor, α, β, ply), nextValue = ref4[0], _ = ref4[1];
      if (nextValue > v) {
        ref5 = [nextValue, action], v = ref5[0], a = ref5[1];
      }
      if (v >= β) {
        return [v, a];
      }
      α = Math.max(α, v);
    }
    return [v, a];
  };

  MinimaxAgent.prototype.mini = function(state, α, β, ply) {
    var _, a, action, i, len, nextValue, ref2, ref3, ref4, ref5, successor, v;
    ref2 = [+Limit, null], v = ref2[0], a = ref2[1];
    ref3 = state.possibleActions();
    for (i = 0, len = ref3.length; i < len; i++) {
      action = ref3[i];
      successor = state.play(action);
      ref4 = this.minimax(successor, α, β, ply), nextValue = ref4[0], _ = ref4[1];
      if (nextValue < v) {
        ref5 = [nextValue, action], v = ref5[0], a = ref5[1];
      }
      if (v <= α) {
        return [v, a];
      }
      β = Math.min(β, v);
    }
    return [v, a];
  };

  return MinimaxAgent;

})();

module.exports = {
  MAX: MAX,
  MIN: MIN,
  Limit: Limit,
  MinimaxAgent: MinimaxAgent
};
